@import url('https://fonts.googleapis.com/css2?family=Italianno&display=swap');


:root {
    --green: #40ba37;
    --darkGreen: #1c8314;
    --black: #000;
    --white: #fff;
}

body {
    overflow-x: hidden;
}

.mb-3 h1 {
    text-shadow: 2px 2px 8px #beb7b7;
}

.column {
    float: left;
    width: 33.3%;
    margin-bottom: 16px;
    padding: 0 8px;
}

.card {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    margin: 8px;
}

.about-section {
    padding: 50px;
    text-align: center;
    background-color: #e2e5eb;
    color: #000;
}

.container {
    padding: 0 16px;
}

.container::after, .row::after {
    content: "";
    clear: both;
    display: table;
}

.title {
    color: grey;
}

.nav-link {
    font-weight: bold;
    text-transform: uppercase;
    padding: 12px 18px 11px;
    background-color: transparent;
    border-bottom: 3px solid transparent;
    line-height: 1;
    color: rgb(37, 34, 34) !important;
    font-weight: 600;
}

.nav-link:hover {
    color: var(--white) !important;
    background-color: var(--green);
    transform: scale(1.1);
    opacity: 0.8;
}

a.logo {
    transition: transform 1s ease;
    font-family: 'Italianno', cursive;
    font-style: italic;
    font-size: 2.7em;
    font-weight: 900;
}

.logo:hover {
    transform: scale(1.2, 1.2);
}

#homelink {
    color: var(--white) !important;
    background-color: var(--green) !important;
    border-bottom: 3px solid var(--darkGreen) !important;
}

#food {
    font-size: 14px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 10;
    font-style: normal;
    display: flex;
    margin-top: -1em;
    letter-spacing: 0.5em;
}

.search {
    margin-left: 25%;
    cursor: pointer;
}

.search i {
    color: rgb(124, 116, 116) !important;
}

.button {
    border: none;
    outline: 0;
    display: inline-block;
    padding: 8px;
    color: white;
    background-color: #000;
    text-align: center;
    cursor: pointer;
    width: 100%;
}

.button:hover {
    background-color: #555;
}

i:hover {
    width: 20%;
}

li i.fa:hover {
    transform: scale(1.5, 1.5);
}

.scrollToTop {
    background: rgba(0, 0, 0, 0.1);
    bottom: 15px;
    display: none;
    height: 50px;
    padding: 2px;
    position: fixed;
    right: 20px;
    text-align: center;
    text-decoration: none;
    width: 50px;
    z-index: 99999;
}

.scrollToTop i {
    color: var(--white);
    font-size: 40px;
}

.scrollToTop:hover {
    background-color: var(--green);
    border-bottom: 3px solid var(--darkGreen) !important;
}

@media screen and (max-width: 650px) {
    .column {
        width: 100%;
        display: block;
    }
}